import * as React from "react";
import { Link as GatsyLink } from "gatsby";
import ParseUrl from "url-parse";
import {cn} from "../../../classnames";

const okDomains = new Set([
    "localhost",
    "petitsanimaux.github.io",
    "petitsanimaux",
    "crochetnest.com",
    "crochetnest",
]);

const isCurDomain = (href) => {
    const url = new ParseUrl(href);
    return okDomains.has(url.hostname);
}

function detectURLs(message) {
    const urlRegex = /https?:\/\/(www\.)?[-a-zA-Z0-9@:%._\+~#=]{1,256}\.[a-zA-Z0-9()]{1,6}\b([-a-zA-Z0-9()@:%_\+.~#?!&//=]*)/gi;
    return message.match(urlRegex);
}

const ThemedLink = (props) => {
    const {className = '', disabled, href, useTheming = true} = props;
    if (!href) {
        return null;
    }

    const classname = useTheming ? cn(props, "Link") : cn(props, "");
    const urls = detectURLs(href);
    const url = urls && urls.length > 0 ? urls[0] : href;
    if (isCurDomain(url)) {
        return (<GatsyLink className={classname} to={disabled ? "#" : url}>{props.children}</GatsyLink>);
    }

    return (
        <a className={classname} target="_blank" href={disabled ? "#" : url}>{props.children} </a>
    );
}

export default ThemedLink;