import React from "react";
import Link from "../../components/Link";
import AddToCartButton from "../AddToCartButton";

import useIsEnabled, {FLAGS} from "../../../hooks/useIsEnabled";
import "./styles.scss";

const StripeBuyButton = (props) => {
    const isCartEnabled = useIsEnabled(FLAGS.CART);
    const {id, priceId, stripeUrl} = props;

    if (isCartEnabled && priceId) {
        return (
            <AddToCartButton priceId={priceId} id={id} />
        );
    }

    const str = 'Buy with Stripe';
    return (
        <Link className="StripeBuyButton" href={stripeUrl} useTheming={false}>{str}</Link>
    )
}

export default StripeBuyButton;